@each $label, $map in $week-list {
	@if (map-get($map, week) == week4) {
		@keyframes #{$label} {
			0% { background-position: #{map-get($map, posFirst)} 0}
			69.4% { background-position: #{map-get($map, posLast)} 0}
			100% { background-position: #{map-get($map, posLast)} 0}
		}
	}
	@else {
		@keyframes #{$label} {
			0% { background-position: #{map-get($map, posFirst)} 0 }
			100% { background-position: #{map-get($map, posLast)} 0}
		}
	}

	.monster{
		&.#{$label}{
			animation: #{$label} #{map-get($map, timer)}s steps(#{map-get($map, step)}) #{map-get($map, count)} forwards;
			background: url("../../images/sprite/#{map-get($map, img)}.png") no-repeat 0 0;
		}
	}
}


