@keyframes bounceIn {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	70% {
		opacity: 0;
		transform: scale(0);
	}
	75% {
		opacity: .6;
		transform: scale(0);
	}
	80% {
		transform: scale(.7);
	}
	85% {
		transform: scale(1.2);
	}
	90% {
		transform: scale(.9);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

body.pc{
	#wrap{
		&.main{
			.content-wrap{
				//footer
				.footer{
					.content{
						.week-mission{
							.btn{
								&:hover{
									.icon{
										@for $i from 1 through 6 {
											&.icon-btn0#{$i}{
												&.default{background-image: url("../../images/main/week/icon_week_icon0#{$i}_active.png");}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}


#wrap{
	&.main{background: url("../../images/common/content_bg01.jpg") no-repeat 50% 50%;
		.wrap-outer{background: url("../../images/common/content_bg_inner01.png") no-repeat 50% -20px;
			.wrap-inner{background: url("../../images/main/bg01.jpg") no-repeat 0 0;}
		}
		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			//visual
			.visual{
				&__area{width: 580px;margin: 0 auto;padding-top: 63px;}
				&__box{}
				.monster{width: 580px;height: 530px;}
				.touch-guide{position: absolute;top: 50%;left: 50%;
					.btn-touch{position: absolute;top: -74px;left: -74px;width: 131px;height: 148px;animation: touch-icon 0.8s steps(4) infinite forwards;background: url("../../images/sprite/touch.png") no-repeat 0 0;z-index: 2;}
				}
			}
			//footer
			.footer{position: absolute;
				&.purchase{bottom: 13px;left: 21px;width: 986px;}
				&.unpurchase{bottom: 13px;left: 37px;width: 970px;
					.content{
						.touch-guide{
							.btn-touch{margin-left: -409px;}
							.week-mission__item{margin-left: -425px;}
						}
						.swiper{padding: 0 51px;width: 822px;
							.week-mission{
								&__box{}
								&__item{margin-left: 26px;}
							}
						}
					}
				}
				&__area{}
				&__box{}
				.header{margin-left: 51px;
					&__box{}
					&__txt{width: 213px;height: 46px;border-radius: 20px;background-color: #fa6d84;display: flex;align-content: center;justify-content: center;
						.number{padding-top: 11px;margin-left: 10px;
							.icon{float: left;}
						}
					}
				}
				.content{margin-top: 10px;height: 143px;
					.touch-guide{display: none;position: fixed;top: 0;left: 0;right: 0;bottom: 0;min-width:1024px;background-color: rgba(16,41,54,0.85);z-index: $z-layer1;
						.btn-touch{position: absolute;top: 650px;left: 50%;width: 131px;margin-left: -429px;height: 148px;animation: touch-icon 0.8s steps(4) infinite forwards;background: url("../../images/sprite/touch.png") no-repeat 0 0;}

						.week-mission__item{position: absolute;top: 631px;left: 50%;margin-left: -445px;
							.btn{
								.icon{display: block;width: 123px;height: 123px;background-repeat: no-repeat;background-position: 0 0;transition: background 0.3s;
									&.icon-btn01{
										&.default{background-image: url("../../images/main/week/icon_week_icon01.png");
											&.active{background-image: url("../../images/main/week/icon_week_icon01_active.png");}
										}
									}
								}
							}
						}
					}
					.swiper{position: relative;padding: 0 46px;width: 865px;box-sizing: border-box;
						&-container{margin-top: -20px;}
						&-wrapper{padding-top: 20px;}
						&-controller{
							.btn-swiper-prev{position: absolute;top: 50%;left: 0;width: 37px;height: 62px;margin-top: -21px;background: url("../../images/main/week/btn_week_arrow_left01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;
								&.swiper-button-disabled{opacity: 0;}
							}
							.btn-swiper-next{position: absolute;top: 50%;right: 0;width: 37px;height: 62px;margin-top: -21px;background: url("../../images/main/week/btn_week_arrow_right01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;
								&.swiper-button-disabled{opacity: 0;}
							}
						}

						.week-mission{
							&__box{}
							&__item{float: left;margin-left: 7px;
								&:nth-of-type(1){margin-left: 0;}
							}
							.week-mission__item{
								&:first-child{}
							}
							.btn{overflow: visible;display: block;}
							.icon{position: relative;display: block;width: 123px;height: 123px;background-repeat: no-repeat;background-position: 0 0;transition: background 0.3s;
								@for $i from 1 through 6 {
									&.icon-btn0#{$i}{
										&.default{background-image: url("../../images/main/week/icon_week_icon0#{$i}.png");
											&.active{background-image: url("../../images/main/week/icon_week_icon0#{$i}_active.png");}
										}
										&.complete{background-image: url("../../images/main/week/icon_week_icon0#{$i}_com.png");
											&:before{content:"";position: absolute;top: -17px;left: -11px;width: 92px;height: 78px;animation: icon_week_com 0.7s steps(7) infinite forwards;background: url("../../images/sprite/icon_week_com.png") no-repeat 0 0;}
										}
										&.non-complete{background-image: url("../../images/main/week/icon_week_icon0#{$i}_n-com.png");
											&:before{content:"";position: absolute;top: 2px;left: 9px;width: 42px;height: 42px;background: url("../../images/main/week/icon_week_n-com01.png") no-repeat 0 0;}
										}
									}
								}
							}
						}
					}
					.clubhouse{position: absolute;bottom: 0;right: 0;
						&__area{}
						&__box{}
						&__item{}
						.btn{display: block;
							&:hover{
								.icon{
									&.icon-clubhouse{
										&:before{opacity: 1;}
									}
								}
							}
							.icon{
								&.icon-clubhouse{display: block;width: 121px;height: 149px;background: url("../../images/main/week/btn_clubhouse01.png") no-repeat 0 0;
									&:before{content:"";position: absolute;top: 0;left: 0;width: 121px;height: 149px;background: url("../../images/main/week/btn_clubhouse01_active.png") no-repeat 0 0;opacity: 0;transition: all 0.3s;}
								}
							}
						}
					}
				}
			}
		}
	}
}

.number{display: inline-block;
	.icon{display: inline-block;
		&-num0{@include sprite($main_week_number-num0)}
		&-num1{@include sprite($main_week_number-num1)}
		&-num2{@include sprite($main_week_number-num2)}
		&-num3{@include sprite($main_week_number-num3)}
		&-num4{@include sprite($main_week_number-num4)}
		&-num5{@include sprite($main_week_number-num5)}
		&-num6{@include sprite($main_week_number-num6)}
		&-num7{@include sprite($main_week_number-num7)}
		&-num8{@include sprite($main_week_number-num8)}
		&-num9{@include sprite($main_week_number-num9)}
	}
}

.monster-video{display: none;position: absolute;top: 0;left: 0;width: 1024px;height: 768px;z-index: $z-layer1}
